/* start of mobile nav */
/* mnav fulmore title */
.mnav-mdivider h3{
    font-family: 'Rock Salt', cursive;
    color: #8c8c8c;
    font-size: 2.5vw;
    padding-top: 30px;
    /* padding-left: 3.5vw; */
