}
.mnav-container{
    display: none;
    background-color:#EEEEEE;
    position: absolute;
    z-index: 2;
    top: 0;
    right: 0;
    padding-left: 15px;
    padding-right: 15px;
    width: 50vw;
    height: 100vh;
    -webkit-transform: translateX(50vw);
            transform: translateX(50vw);
    transition: -webkit-transform 400ms ease-in 0s;
    transition: transform 400ms ease-in 0s;
    transition: transform 400ms ease-in 0s, -webkit-transform 400ms ease-in 0s;
}
.mnav-mdivider{
    background-color: #EEEEEE;
    height: 120px;
    width: 100%;
    text-align: center;
}
.mnav-mdivider a:hover{
    text-decoration: none;
}
.menu-bottom-divider{
    background-color: #EEEEEE;
    height: 100%;
