}
/* sub menu drop on mobile */
.sub-menu-one, .sub-menu-two{
    list-style: none;
    font-size: 1.8vw;
    display: none;
}
.sub-menu-one li, .sub-menu-two li{
    padding-left: 10px;
}
.sub-link, .sub-link-two{
    opacity: 0;
    transition: opacity 500ms ease-in 0s;
}
.sub-link-active{
    opacity: 1;
}
/* js toggle burger menu */
.mnav-active{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
.menu-one-active, .menu-two-active{
    display: block;
