/* start of drop down on hover */
.portfolio-dp, .info-dp{
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.portfolio-content, .info-content{
    margin-top: 2.7vw;
    display: none;
    position: absolute;
    opacity: 0;
    transition: visibility 0s, opacity 0.25s linear;
    background-color: #EEEEEE;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

.dp-link{
    color: #8c8c8c !important;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-transform: capitalize;
}

.portfolio-content a:hover, .info-content a:hover{
    background-color: #ddd;
}
.portfolio-dp:hover .portfolio-content{
    display: block;
    opacity: 1;
}
.info-dp:hover .info-content{
    display: block;
    opacity: 1;
}
/* end of drop down on hover */


